
import indexNew from "@/views/index/indexNew";
import tabContent from "@/views/index/tabContent";
import vFooter from "@/components/v-footer";
import VWechatshare from "@/components/v-wechatshare";
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import {
mapState
} from "vuex";
export default {
data() {
return {
listFun: [1,2,3,4,5,6],
index: 0,
onTab: 0,
getBarWidth: function () {
return 66 + 'px'
}
};
},
components: {
"v-footer": vFooter,
"v-wechatshare": VWechatshare,
indexNew,
tabContent,
Tab,
TabItem,
Swiper,
SwiperItem,
tabContent,
indexNew
},
beforeCreate() {
document.title = "新银众商";
},
created() {
this.getAllClassTree();
this.getClassBylevel();
},
computed: {
...mapState(["weChatInfo", "weChatShare"])
},
methods: {
onTabClick(item){
this.onTab = item.id;
console.log(this.onTab);
},
getClassBylevel(){
this.$axios.get(this.api.getClassBylevel+1)
.then(res => {
if(res.data.code === 1){
this.listFun = [];
res.data.content.unshift({'id':0,'className': "商城首页"});
this.listFun = res.data.content;
}
console.log(res.data);

})
.catch(res => {

});
},
getAllClassTree(){
this.$axios.get(this.api.getClassTree)
.then(res => {
console.log(res.data);
})
.catch(res => {

});
}
}
};



