
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import vTitle from "@/components/v-title";
import swiperPic from "@/assets/images/banner@2x.png";
const qs = require("qs");
export default {
props : ['parentId'],// 接收父组件传过来的值
data() {
return {
urlPic:this.api.urlPic,
parentIds: this.parentId,
totalPage: 1,
currentPage: 0,
listData:[],
classId: 0,
loadMoreVal: true,
pullupEnabled: true,
status: {
pullupStatus: 'default'
},
swiperPic,
list3: [],
items:'',
index3: 0,
bannerSwiper: {
speed: 800,
auto: 3000,
showIndicators: true,
continuous: true,
link: "/goods",
arrData: []
},
dailySwiper: {
interval: 3000,
duration: 800,
direction: "up",
height: document.body.offsetWidth/7.5*0.77,
arrData: []
}
};
},
components: {
Grid,
GridItem,
Tab,
TabItem,
Swiper,
SwiperItem,
Scroller,
Spinner,
vTitle,
Flexbox,
FlexboxItem
},
beforeCreate() {

},
created() {

this.getTab(this.parentId);
this.loadMore(this.classId);
console.log(this.parentId);
},
computed:{

},
watch:{// 监听值的变化
parentId (val, oldVal) {
console.log(val, oldVal);
this.getTab(val);
}
},
methods: {
getTab (id){
this.$axios.get(this.api.getChildsList+id)
.then(res => {
if(res.data.code === 1){
console.log(res.data);
if(res.data.content.length>0){
this.list3 = res.data.content;
this.items = res.data.content[0].className;
this.classId = res.data.content[0].id;
this.onTabsClick(res.data.content[0]);
}
}
})
.catch(res => {

});
},
loadMore (classId) {
if(this.loadMoreVal){
this.loadMoreVal=false;
setTimeout(()=>{
this.loadMoreVal=true;
},1000)
}else{
return;
}
if(this.currentPage< this.totalPage){
this.currentPage= parseInt(this.currentPage) + 1;
this.$axios.post(this.api.getClassList,qs.parse({'goodsParentClassId':classId, "page" : this.currentPage, "limit":8 }),{headers: {"content-type": "application/json"}})
.then(res => {
console.log(res.data);
this.totalPage=res.data.content.totalPage;
this.listData = [...this.listData,...res.data.content.list];
console.log(this.listData);
})
.catch(res => {

});
}else{
this.status.pullupStatus = 'complete';
setTimeout(()=>{
this.status.pullupStatus = 'default';
},1000);
console.log('已加载完');
}
setTimeout(() => {

}, 10)
},
onTabsClick(item){
this.items = item.className;
console.log(this.items);
this.listData = [];
this.totalPage = 1;
this.currentPage = 0;
this.loadMore(item.id);
},
refresh () {
console.log('刷新');
this.$nextTick(() => {
setTimeout(() => {
this.$refs.scroller.donePulldown()
this.pullupEnabled && this.$refs.scroller.enablePullup()
}, 10)
})
},
toDetail (id) {
console.log(21);
this.$router.push({//核心语句
path:'/goods',//跳转的路径
query:{//路由传参时push和query搭配使用 ，作用时传递参数
id: id
}
})
}
}
};



